.wrapper {
  height: 70px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 2px;
}

.nav {
  display: flex;
  height: 100%;
}

.nav-list {
  display: flex;
  list-style: none;
  align-items: center;
}

.nav-item {
  padding: 8px 12px;
  margin: 0 12px;
  font-size: 1.7rem;
}

.btn-quote {
  width: 230px;
  background-color: #fff;
  border-radius: 14px;
  border: 1px solid var(--dark);
  font-size: 1.7rem;
}
